button{
    display: inline-block;
    position: relative;
    border: none;
    color: #FF8A65;
    text-shadow: 0 5px 5px rgba(0, 0, 0, 5);
    font-size: 18px;
    cursor: pointer;
    margin: 20px 30px;
    border-radius: 25px;
    background: linear-gradient(145deg, #2a333a, #232b31);
    box-shadow:  6px 6px 12px #101316,
    -6px -6px 12px #3e4d56;
}

.span {
    display: block;
    padding: 25px 50px;
}

button::before, button::after {
    content:"";
    width: 0;
    height: 2px;
    position: absolute;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
    background: #FF8A65;
}

.span::before, .span::after{
    content:"";
    width:3px;
    height:0;
    position: absolute;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
    background: #FF8A65;
}

button:hover::before, button:hover::after{
    width: 100%;
}
button:hover span::before, button:hover span::after{
    height: 100%;
}


/*----- button 1 -----*/
.btn-1::before, .btn-1::after{
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
}
.btn-1 span::before, .btn-1 span::after{
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}
.btn-1::before{
    right: 0;
    top: 0;
}
.btn-1::after{
    left: 0;
    bottom: 0;

}
.btn-1 span::before{
    left: 0;
    top: 0;
}
.btn-1 span::after{
    right: 0;
    bottom: 0;

}
.btn-1:hover::before, .btn-1:hover::after{
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}
.btn-1:hover span::before, .btn-1:hover span::after{
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
}

.btn-2::before, .btn-2::after{
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}
.btn-2 span::before, .btn-2 span::after{
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
}
.btn-2::before{
    right: 0;
