/*BUTTON CSS*/

.btn-3{
    display: inline-block;
    position: relative;
    border: none;
    color: #FF8A65;
    text-shadow: 0 5px 5px rgba(0, 0, 0, 5);
    font-size: 18px;
    cursor: pointer;
    margin: 20px 30px;
    background: linear-gradient(145deg, #232b31, #2a333a);
    box-shadow:  5px 5px 12px #101316,
    -5px -5px 12px #3e4d56;
    border-radius: 10px;
}

.span-1 {
    display: block;
    padding: 5px 5px;
}

.btn-3::before, .btn-3::after {
    content:"";
    width: 0;
    height: 2px;
    position: absolute;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
    background: #FF8A65;
}

.span-1::before, .span-1::after{
    content:"";
    width:3px;
    height:0;
    position: absolute;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
    background: #FF8A65;
}

.btn-3:hover::before, .btn-3:hover::after{
    width: 100%;
}
.btn-3:hover .span-1::before, .btn-3:hover .span-1::after{
    height: 100%;
}

.btn-3::before, .btn-3::after{
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
}
.btn-3 span::before, .btn-3 span::after{
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}
.btn-3::before{
    right: 0;
    top: 0;
}
.btn-3::after{
    left: 0;
    bottom: 0;

}
.btn-3 .span-1::before{
    left: 0;
    top: 0;
}
.btn-3 .span-1::after{
    right: 0;
    bottom: 0;

}
.btn-3:hover::before, .btn-3:hover::after{
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}
.btn-3:hover .span-1::before, .btn-3:hover .span-1::after{
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
}


