
.about-header{
    top: 50%;
    left: 50%;
    margin-top: 50px;
    display: inline-block;
    text-align: center;
}

.title{
    font-size: 35px;
    margin-top: 0;
    margin-left: 0;
    margin-bottom: 0;
    display: inline-block;
    text-align: left;
    color: #FF8A65;
    text-shadow: 0 5px 5px rgba(0, 0, 0, 5);
}

.content-wrapper {
    display: inline-block;
    position: relative;
    padding: 10px;
    margin: 50px;
    border: 5px outset #FF8A65;
    border-radius: 50px;
    box-shadow:   27px 27px 54px #14181b,
    -27px -27px 54px #3b4851;
}

@media (max-width: 768px) {
    .profile-picture
    {
        display: inline-block;
        position: relative;
        text-align: left;
        width: auto;
        top: 0;

    }

    .Description-container {
        display: inline-block;
        position: relative;
        width: 100%;
        float: left;
    }
}

@media (min-width: 1368px) {
    .profile-picture
    {
        padding: 50px;
        height: 100%;
        width: 50%;
        display: inline-block;
        position: relative;
        float: left;
        text-align: left;
        width: auto;
        border-radius: 12px;
    }

    .Description-container
    {
        display: inline-block;
        margin: 15px;
        position: relative;
        width: 50%;
        float: left;
    }

}

.prof-picture {
    max-width: 480px;
    /*max-height: 560px;*/
    width: 100%;
    height: 100%;
    border-radius: 40px;

    /*background: #c4c4c4;*!*/
    /*border-radius: 85px;*/
    /*box-shadow: 0 11px 11px 0 rgba(0,0,0,0.7);*/
    padding: 10px;

}

.text-content {
    color: #FF8A65;
    text-shadow: 0 5px 5px rgba(0, 0, 0, 2);
    text-align: justify;
}

.text-header {
    color: #FF8A65;
    text-shadow: 0 5px 5px rgba(0, 0, 0, 2);
}

.text-typing {
    color: #FF8A65;
    text-shadow: 0 5px 5px rgba(0, 0, 0, 2);
}